import React, { useEffect, useState } from "react";
import "./Project_NavBar.css";
import covid19 from "../../images/covid19_logo.png";
import hcj_logo from "../../images/html_css_js_logo.png";
import huffman_logo from "../../images/huffman_code_generator_logo.png";
import blood_donation_logo from "../../images/blood_donation_logo.png";

const navBarData = [
  {
    img: covid19,
    text: "Covid19 Case Statistics",
  },
  {
    img: hcj_logo,
    text: "Online Code Compiler",
  },
  {
    img: blood_donation_logo,
    text: "Blood Donation Website",
  },
  {
    img: huffman_logo,
    text: "Huffman Code Generator",
  },
];

const Project_Navbar = (props) => {
  const [active, setActive] = useState([true, false, false, false]);

  const makeActive = (index) => {
    props.setProjectIndex(index);
    let navbar_item = document.getElementsByClassName("project-navbar-item");
    setActive((ac) =>
      ac.map((a, i) => (index === i ? (a = true) : (a = false)))
    );

    for (let i = 0; i < active.length; i++) {
      if (index === i)
        navbar_item[i].lastChild.lastChild.classList.add("active");
      else navbar_item[i].lastChild.lastChild.classList.remove("active");
    }
  };
  useEffect(() => {
    if (active[0])
      document
        .getElementsByClassName("project-navbar-item")[0]
        .lastChild.lastChild.classList.add("active");
  }, []);

  return (
    <div className="project-navbar">
      <div className="project-navbar-list">
        {navBarData &&
          navBarData.map((ele, index) => {
            return (
              <div
                className="project-navbar-item"
                key={index}
                onClick={() => makeActive(index)}
              >
                <div className="project-navbar-img">
                  <img src={ele.img} alt="" />
                </div>
                <div className="project-navbar-text">
                  <span>{ele.text}</span>
                </div>
              </div>
            );
          })}
      </div>
    </div>
  );
};

export default Project_Navbar;
