import React, { useState } from "react";
import Project_Details from "./ProjectComponents/Project_Details";
import Project_Navbar from "./ProjectComponents/Project_NavBar";
import "./Projects.css";

const Projects = () => {
  const [projectIndex, setProjectIndex] = useState(0);

  return (
    <section id="projects">
      <h1>Projects</h1>
      <div className="project-parent-container">
        <Project_Navbar
          projectIndex={projectIndex}
          setProjectIndex={setProjectIndex}
        />
        <Project_Details
          projectIndex={projectIndex}
          setProjectIndex={setProjectIndex}
        />
      </div>
      <div className="download-resume">
        <a
          href="https://drive.google.com/file/d/1MMzbK62MCks59lpRi5MKTU8BxSKPnP-S/view?usp=sharing"
          target="_blank"
        >
          Download Resume
        </a>
      </div>
    </section>
  );
};

export default Projects;
