import React, { useRef } from "react";
import "./Contact.css";

import emailjs from "@emailjs/browser";
const Contact = () => {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm(
        "service_3wr510r",
        "template_1beu8sr",
        form.current,
        "aYFvSvdhSoE09kddf"
      )
      .then(
        (result) => {
          console.log(result.text);
        },
        (error) => {
          console.log(error.text);
        }
      );
  };

  return (
    <section id="contact">
      <div className="contact-me-header">
        <div className="contact-me-header-top">
          <h2>Contact Me</h2>
          <span></span>
        </div>
        <div className="contact-me-header-bottom">
          Ask whatever you want to know
        </div>
      </div>
      <div className="contact-me-container">
        <div className="contact-me-card">
          <form ref={form} onSubmit={sendEmail}>
            <label htmlFor="name">Name</label>
            <input
              type="text"
              name="user_name"
              id="name"
              placeholder="Enter your name"
            />
            <label htmlFor="Email">Email</label>
            <input
              type="email"
              name="from_name"
              id="email"
              placeholder="Enter your email"
            />
            <label htmlFor="message-textarea">Message</label>
            <textarea
              spellCheck={true}
              name="message"
              placeholder="Enter your message"
              id="message-textarea"
            ></textarea>
            <input type="submit" id="send-me-btn" value="Send me" />
          </form>
        </div>
        <div className="mapouter">
          <div className="gmap_canvas">
            <iframe
              className="gmap_iframe"
              frameBorder="0"
              scrolling="no"
              marginHeight="0"
              marginWidth="0"
              src="https://maps.google.com/maps?width=400&amp;height=300&amp;hl=en&amp;q=BulBul chati, Kaushallya&amp;t=&amp;z=15&amp;ie=UTF8&amp;iwloc=B&amp;output=embed"
            ></iframe>
          </div>
          <div className="current-location">
            <div className="address">
              <b>Address : </b>
              <small>
                Bulbulchati near kali mandir, Kaushallya, Kharagpur, Paschim
                Medinipur, West Bengal
              </small>
            </div>
            <div className="pincode">
              <b>Pincode : </b>721301
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact;
