import React from "react";
import "./NavBar.css";

const Navbar = ({ responsive, setResponsive }) => {
  const setNavBar = () => {
    setResponsive(!responsive);
  };

  return (
    <div className="navbar">
      <nav>
        <h1>SUBHODIP11</h1>
        <ul className="nav-items">
          <li>
            <a href="#home">Home</a>
          </li>
          <li>
            <a href="#about">About</a>
          </li>
          <li>
            <a href="#carrer">Carrer</a>
          </li>
          <li>
            <a href="#projects">Projects</a>
          </li>
          <li>
            <a href="#contact">Contact me</a>
          </li>
        </ul>
      </nav>
      <nav className="nav-responsive">
        <h1>SUBHODIP11</h1>
        <button onClick={setNavBar}>|||</button>
        {responsive && (
          <ul className="nav-items-responsive">
            <li>
              <a href="#home">Home</a>
            </li>
            <li>
              <a href="#about">About</a>
            </li>
            <li>
              <a href="#carrer">Carrer</a>
            </li>
            <li>
              <a href="#projects">Projects</a>
            </li>
            <li>
              <a href="#contact">Contact me</a>
            </li>
          </ul>
        )}
      </nav>
    </div>
  );
};

export default Navbar;
