import React, { useState } from "react";
import "./Carrer.css";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Bar } from "react-chartjs-2";
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);
const options = {
  responsive: true,
  plugins: {
    legend: {
      position: "top",
    },
    title: {
      display: true,
      text: "Carrier Statistics till date",
    },
  },
};
const labels = [
  "X",
  "XII",
  "1st sem",
  "2nd sem",
  "3rd sem",
  "4th sem",
  "5th sem",
];
const marks = [93.1, 91.8, 98.2, 95.0, 98.2, 93.6, 95.7];
const data = {
  labels,
  datasets: [
    {
      label: "Percentage per standard",

      data: labels.map((ele, index) => marks[index]),
      backgroundColor: [
        "#1c1c6b",
        "#1c1c6b",
        "rgb(60,60,140)",
        "rgb(60,60,140)",
        "rgb(60,60,140)",
        "rgb(60,60,140)",
        "rgb(60,60,140)",
      ],
    },
  ],
};

const Carrer = () => {
  return (
    <section id="carrer">
      <h1>Academic Insights</h1>
      <div className="carrer-container">
        <div className="Barchart-container">
          <Bar data={data} options={options} />
        </div>
        <div className="carrier-insights">
          <table>
            <caption>School Statistics</caption>
            <thead>
              <tr>
                <th scope="col">Class</th>
                <th scope="col">Marks(in %)</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>X</td>
                <td>93.1 %</td>
              </tr>
              <tr>
                <td>XII</td>
                <td>91.8 %</td>
              </tr>
              <tr>
                <td>CGPA</td>
                <td>{((93.1 + 91.8) / 2 / 9.5).toFixed(2)} </td>
              </tr>
            </tbody>
          </table>
          <table>
            <caption>College Statistics</caption>
            <thead>
              <th>Semester</th>
              <th>Marks( SGPA )</th>
            </thead>
            <tbody>
              <tr>
                <td>
                  1<sup>st</sup>
                </td>
                <td>9.82</td>
              </tr>
              <tr>
                <td>
                  2<sup>nd</sup>
                </td>
                <td>9.5</td>
              </tr>
              <tr>
                <td>
                  3<sup>rd</sup>
                </td>
                <td>9.82</td>
              </tr>
              <tr>
                <td>
                  4<sup>th</sup>
                </td>
                <td>9.36</td>
              </tr>
              <tr>
                <td>
                  5<sup>th</sup>
                </td>
                <td>9.57</td>
              </tr>
              <tr>
                <td>CGPA</td>
                <td>9.61 </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </section>
  );
};

export default Carrer;
