import React from "react";
import Me from "../images/Me.jpg";
import "./About.css";
const About = () => {
  return (
    <section id="about">
      <div className="header">About Me</div>
      <div className="about-card">
        <div className="image-wrapper">
          <img id="me" src={Me} alt="" />
        </div>
        <div className="about-details-container">
          <div className="about-header">Want to know more about me ?</div>
          <div className="about-details">
            <p>
              I am a student at Institute of Technical Education and Research
              (ITER), SOA University, pursuing B.Tech in CSE 3<sup>rd</sup>{" "}
              year.
              <br />I am a self-learnt full stack web developer, entirely
              learned from open source articles and youtube.
            </p>
            <br />
            <h3>Here are a few highlights :-</h3>
            <ul className="about-details-ul">
              <li>Participated in various hackathons and competitions.</li>
              <li>Practiced more than 500 competitive coding questions.</li>
              <li>Member of codex(Coding community of ITER). </li>
              <li>
                Developed more than 20 minor projects and 5 major projects with
                Java and Nodejs as backend and Reactjs at frontend.
              </li>
              <li>Have a good knowledge in Data Structures and Algorithms.</li>
              <li>
                Done several internships and worked on different tech stacks
              </li>
            </ul>

            <br />

            <p>
              Still trying to make my coding practices more enhanced and better.
            </p>
            <div className="about-contact-me-btn">
              <a
                href="https://drive.google.com/file/d/1MMzbK62MCks59lpRi5MKTU8BxSKPnP-S/view?usp=sharing"
                target="_blank"
              >
                Get Resume
              </a>
            </div>
          </div>
        </div>
      </div>

      <div className="hackathons-awards">
        <div className="hackathons">
          <h2>Hackathons</h2>
          <ul>
            <li>Particiapted in Google Hashcode 2022</li>
            <li>
              Participated in Green hackthon 2021 and cleared upto 2
              <sup>nd</sup> round.
            </li>
          </ul>
        </div>
        <div className="internships">
          <h2>Internships</h2>
          <ul>
            <li>
              <div className="internship-header">
                <h4>BUZZLE</h4>
                <span className="internship-date">MAR 2022</span>
              </div>
              <p>
                Worked as a full stack web developer at Buzzle organisation.
                Created user interfaces using React.js and module css.
              </p>
            </li>
            <li>
              <div className="internship-header">
                <h4>VIRTUSA</h4>
                <span className="internship-date">JUNE 2022 - PRESENT</span>
              </div>
              <p>
                Got selected as an intern at Virtusa through their Student
                Ambassador Program.{" "}
              </p>
            </li>
          </ul>
        </div>
      </div>
    </section>
  );
};

export default About;
