import React from "react";
import "./Footer.css";
import instagram from "../images/instagram.png";
import whatsapp from "../images/whatsapp.png";
import github from "../images/github.svg";
import gmail from "../images/gmail.png";
const icons = [
  {
    title: "@subhodip_chatterjee11",
    href: "https://www.instagram.com/?hl=en",
    src: instagram,
  },
  {
    title: "subhodip12112000@gmail.com",
    href: "https://mail.google.com/mail",
    src: gmail,
  },
  {
    title: "Subhodip11",
    href: "https://github.com/Subhodip11",
    src: github,
  },
  {
    title: "+91 8101190884",
    href: "https://www.whatsapp.com/",
    src: whatsapp,
  },
];
const Footer = () => {
  return (
    <section id="footer">
      <div className="copyright">
        Copyright &#169; - Subhodip Chatterjee. All rights reserved (2022)
      </div>
      <div className="contact-info">
        {icons.map((ele, index) => {
          return (
            <a href={ele.href} key={index}>
              <img src={ele.src} title={ele.title} alt="" />
            </a>
          );
        })}
      </div>
    </section>
  );
};

export default Footer;
