import React from "react";
import "./Project_Details.css";

const project_data = [
  {
    title: "COVID19 CASE STATISTICS",
    timeSpan: "AUG 2021",
    techUsed: "Node.js, Express.js, EJS, Axios(for API call)",
    keyPoints: [
      "Every Day Covid-19 Case Statistic",
      "Vaccine Search with filters available, Vaccine Search via Pin",
      "To know cases for a state just click on the state name",
    ],
    projectLink: "https://covid19casestatistics.herokuapp.com/",
    github: "https://github.com/Subhodip11/covid19-app",
  },
  {
    title: "ONLINE COMPILER",
    timeSpan: "FEB 2022",
    techUsed: "React.js",
    keyPoints: [
      "An online HTML, CSS and JS compiler with dynamic window to preview the code",
      "You can also download and save your code!",
    ],
    projectLink: "https://subhodip11.github.io/code-digger",
    github: "https://github.com/Subhodip11/code-digger",
  },
  {
    title: "BLOOD DONATION",
    timeSpan: "APR 2021",
    techUsed: "Node.js, Express.js, EJS, MongoDB, Bootstrap, Material UI",
    keyPoints: [
      "User can search for the required blood group or can register to donate blood",
      "Admin can add, delete or update details of registered user",
    ],
    projectLink: "https://nodejs-blood-doner-app.herokuapp.com/index",
    github: "https://github.com/Subhodip11/blood-donation-app",
  },
  {
    title: "FILE ENCODER",
    timeSpan: "OCT 2021",
    techUsed: "Node.js, Express.js, EJS, Huffman Encoding Algorithm (DSA)",
    keyPoints: [
      "User can enter text to get it’s equivalent encrypted code",
      "User can use frequency table to get back the text",
    ],
    projectLink: "https://huffmancodegenerator.herokuapp.com/",
    github: "https://github.com/Subhodip11/Huffman-Code-Generator",
  },
];

const Project_Details = (props) => {
  return (
    <div className="project-details">
      <div className="project-details-title">
        <h2>{project_data[props.projectIndex].title}</h2>
        <span>{project_data[props.projectIndex].timeSpan}</span>
      </div>
      <div className="project-details-tech-stack">
        <b>Technologies Used :-</b> {project_data[props.projectIndex].techUsed}
      </div>
      <div className="project-details-description">
        <div className="project-details-description-highlights">
          <u>
            <b>
              <i>Key highlights of the website</i>
            </b>
          </u>
        </div>
        <ul>
          {project_data[props.projectIndex].keyPoints.map((ele, i) => {
            return <li key={i}>{ele}</li>;
          })}
        </ul>
      </div>
      <div className="project-link">
        <a target="_blank" href={project_data[props.projectIndex].projectLink}>
          View Project
        </a>
        <a target="_blank" href={project_data[props.projectIndex].github}>
          Github Repo
        </a>
      </div>
    </div>
  );
};

export default Project_Details;
